.game {
    border-radius: 10px;
    border: 2px solid #003f59;
    background-color: #0073a3;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.25);
    overflow: hidden;
}

@media screen and (min-width: 360px) {
    .game {
        border-radius: 20px;
    }
}

@media screen and (min-width: 1400px) {
    .game {
        border-radius: 30px;
    }
}